<template>
  <div class="child-view heigth">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="cover">
        <img-upload @uploadSuccess="uploadSuccess"></img-upload>
      </el-form-item>
      <el-form-item label="" prop="title">
        <el-input
          style="max-width: 300px"
          placeholder=""
          v-model="ruleForm.title"
          clearable>
        </el-input>
      </el-form-item>
      <div id="editor"></div>
    </el-form>
    <div class="btn-grounp">
      <el-button type="info" @click="articleList"></el-button>
      <el-button type="primary" @click="submitEditor"></el-button>
    </div>
  </div>
</template>

<script>
  import VueCropper from 'vue-cropper'
  import imgUpload from "@/components/imgUpload"

  var E = require('wangeditor')  //  npm 
  export default {
    name: "add-article",
    data() {
      return {
        ruleForm: {
          cover: "",
          title: "",
          editor: null,
        },
        rules: {
          cover: [{ required: true }],
          content: [{ required: true }],
          title: [{ required: true, message: '', trigger: 'blur' }],
        },
      }
    },
    methods: {
      articleList() {
        this.$emit("articleList", "articleList")
      },
      uploadSuccess(url) {
        console.log(url);
        this.ruleForm.cover = url
      },
      submitEditor() {
        let that = this
        if (that.ruleForm.editor) {
          let textContent = that.ruleForm.editor.txt.html()
          if(textContent == '' || that.ruleForm.title == '' || that.ruleForm.cover == '') {
            that.$message.info("")
            return
          }
          that.$http.post("/apis/api/article/addArticle", {
            title: that.ruleForm.title,
            content: textContent,
            cover: that.ruleForm.cover,
            username: '12138',
          }).then(function (res) {
            that.$message.success("")
            that.$emit("articleList", "articleList")
          })
        }
      }
    },
    mounted() {
      // 
      this.editor = null
      document.getElementById("editor").innerHTML = "";
      var editor = new E('#editor')
      editor.customConfig.uploadImgServer = '/apis/api/upload'
      editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024
      editor.customConfig.uploadImgMaxLength = 1
      editor.customConfig.menus = [
        'head',  // 
        'bold',  // 
        'fontSize',  // 
        'fontName',  // 
        'italic',  // 
        'underline',  // 
        'strikeThrough',  // 
        'foreColor',  // 
        'backColor',  // 
        // 'link',  // 
        'list',  // 
        'justify',  // 
        // 'quote',  // 
        'emoticon',  // 
        'image',  // 
        // 'table',  // 
        // 'video',  // 
        // 'code',  // 
        //'undo',  // 
        //'redo'  // 
      ]
      editor.customConfig.uploadImgShowBase64 = true
      editor.create()
      this.ruleForm.editor = editor
    },
    components: {
      imgUpload
    }
  }
</script>

<style lang="scss" scoped type="text/scss">
  /deep/ .w-e-toolbar {
    flex-wrap: wrap;
  }

  /deep/ .w-e-text {
    overflow-y: auto;
  }

  /deep/ .w-e-droplist {
    z-index: 20002 !important;
    .w-e-dp-title {
      z-index: 20003 !important;
    }
  }

  .btn-grounp {
    text-align: right;
    padding-top: 15px;
  }
</style>



